<template>

    <div>
    <!-- Projects section -->
    <v-layout justify-left align-left class = "pl-5 pt-12">
      <v-icon size = "40px" color = "orange">mdi-xml </v-icon>
      <h1 class = "pl-3 display-1 white--text">MY PERSONAL PROJECTS</h1>
    </v-layout>

    <v-layout justify-left align-left class = "pl-3 pt-12">
        <v-row>
            <v-col cols = "5">
                <v-expansion-panels dark popout v-model="panelIndex">
                    <v-expansion-panel
                    v-for="project in projects"
                    :key=project.id
                    >
                    <!-- class="grey darken-3" -->
                    <v-expansion-panel-header color="#424242">
                    <div class = "white--text title font-weight-light">{{project.name}}</div>
                    </v-expansion-panel-header>
                        <v-expansion-panel-content style="background:#212121">
                            
                            <v-row justify="start" align="start" class = "pt-6 text-wrap">
                                <v-icon class = "pl-7" size = "20px" color = "orange">mdi-link-variant</v-icon>
                                <p class = "ml-3 white--text text-wrap" style="width: 30rem;">{{project.url}}</p> 
                            </v-row>

                            <v-row justify="start" align="start" >
                                <v-icon class = "pl-7" size = "20px" color = "orange">mdi-tools</v-icon>
                                <p class = "ml-3 white--text ">{{project.technologies}}</p>
                            </v-row>

                            <v-row justify="start" align="start" >
                                <v-icon class = "pl-7" size = "20px" color = "orange">mdi-calendar-range</v-icon>
                                <p class = "ml-3 white--text">{{project.date}}</p>
                            </v-row>

                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-col>

            <v-col cols = "7" class = "pl-12 pt-n12">
                <component :is="projects[panelIndex].content"></component>
            </v-col>

        </v-row>
    </v-layout>

    </div>

</template>



<script>

    import Project_s1 from './Project_s1.vue'
    import Project_s2 from './Project_s2.vue'
    import Project_s3 from './Project_s3.vue'

    export default {
        name: 'Project',

        components: {
            Project_s1,
            Project_s2,
            Project_s3,
        },

        data: () => ({

            panelIndex: 0,
            projects:[
                {
                    id: "0",
                    name: "Online Portfolio - Web Application",
                    date: "Fall 2020",
                    technologies: "Node JS, Vue.JS, Vuetify",
                    url: "N/A",
                    content: "Project_s1",
                },
                {
                    id: "1",
                    name: "Demie's Cake - Web Application",
                    date: "Summer 2020",
                    technologies: "Node JS, Vue.JS, Vuetify, Java, Spring",
                    url: "http://www.hktvlist.com/index.html",
                    content: "Project_s2",
                },
                {
                    id: "2",
                    name: "TV listing - Android Mobile Application",
                    date: "Fall - 2017",
                    technologies: "PHP, Java, Spring",
                    url: "https://play.google.com/store/apps/details?id=com.hktvlisting.v2&hl=en_US",
                    content: "Project_s3",
                },
            ],
        }),

        watch: {
            panelIndex: function(newPanelIndex, oldPanelIndex) {
                //console.log("watch panelIndex id: " + this.panelIndex) 
                //if this panelIndex matches this component's index.. do stuff since we're selected

                if(typeof newPanelIndex === "undefined") {
                    // 
                    this.panelIndex = oldPanelIndex;
                } else {
                    this.panelIndex = newPanelIndex;
                }
            }
        },

        methods: {
        },

    }
</script>

<style scoped>

.text-color {
    color: #faf2f2 !important;
}

</style>
